<template>
  <div class="goods-hot">
    <h3>{{ title }}</h3>
    <div v-if="getsList">
      <GoodsItem v-for="item in getsList" :key="item.id" :goods="item" />
    </div>
  </div>
</template>
<script>
// import { computed } from 'vue'
import GoodsItem from '@/views/category/components/goods-item.vue'
import { findHotGoods } from '@/api/product'
import { computed, ref } from 'vue'
import { useRoute } from 'vue-router'

export default {
  name: 'GoodsHot',
  components: { GoodsItem },
  props: {
    // 热榜类型
    type: {
      type: Number,
      default: 1
    }
  },
  setup (props) {
    const route = useRoute()
    // 类型数据字典
    const types = { 1: '24小时热销榜', 2: '周热销榜', 3: '总热销榜' }
    // 根据传进入的热榜类型，设置标题title
    const title = computed(() => {
      return types[props.type]
    })
    // 发请求获取热榜数据
    const getsList = ref([])
    findHotGoods(route.params.id, props.type).then(data => {
      // console.log('热榜：', data)
      getsList.value = data.result
    })

    return {
      title,
      getsList
    }
  }

}
</script>
<style scoped lang="less">
.goods-hot {
  h3 {
    height: 70px;
    background: @helpColor;
    color: #fff;
    font-size: 18px;
    line-height: 70px;
    padding-left: 25px;
    margin-bottom: 10px;
    font-weight: normal;
  }
  :deep(.goods-item) {
    background: #fff;
    width: 100%;
    margin-bottom: 10px;
    img {
      width: 200px;
      height: 200px;
    }
    p {
      margin: 0 10px;
    }
    &:hover {
      transform: none;
      box-shadow: none;
    }
  }
}
</style>
