 <template>
  <div class="goods-tabs" v-if="goods">
    <nav>
      <a
        @click="activeName = 'GoodsDetail'"
        :class="{ active: activeName === 'GoodsDetail' }"
        href="javascript:;"
        >商品详情</a
      >
      <a
        @click="activeName = 'GoodsComment'"
        :class="{ active: activeName === 'GoodsComment' }"
        href="javascript:;"
        >商品评价<span>({{ goods.commentCount }})</span></a
      >
    </nav>
    <!-- 切换内容的地方 其实是两个组件-->
    <!-- 在vue中动态去切换组件，其实可以使用动态组件component组件 -->
    <!-- is属性用来决定component动态组件渲染为那个组件，组件名称 -->
    <!-- activeName就是 组件的名称 -->
    <!-- keep-alive切换后，保持原组件的数据 -->
    <keep-alive>
      <component :is="activeName"></component>
    </keep-alive>
    <!-- <GoodsDetail v-if="activeName === 'GoodsDetial'" />
    <GoodsComment v-else /> -->
  </div>
</template>
<script>
import GoodsDetail from './goods-detial.vue'
import GoodsComment from './goods-comment.vue'
import { ref, inject } from 'vue'
export default {
  name: 'GoodsTabs',
  components: { GoodsDetail, GoodsComment },
  setup () {
    // GoodsDetial商品详情，GoodsComment商品评价
    const activeName = ref('GoodsDetail')
    // 商品详情数据
    const goods = inject('goods')
    return {
      activeName,
      goods
    }
  }
}
</script>
<style lang="less" scoped>
.goods-tabs {
  min-height: 600px;
  background: #fff;
  nav {
    height: 70px;
    line-height: 70px;
    display: flex;
    border-bottom: 1px solid #f5f5f5;
    a {
      padding: 0 40px;
      font-size: 18px;
      position: relative;
      > span {
        color: @priceColor;
        font-size: 16px;
        margin-left: 10px;
      }
      &:first-child {
        border-right: 1px solid #f5f5f5;
      }
      &.active {
        &::before {
          content: "";
          position: absolute;
          left: 40px;
          bottom: -1px;
          width: 72px;
          height: 2px;
          background: @xtxColor;
        }
      }
    }
  }
}
</style>
