<template>
  <div class="xtx-goods-page">
    <div class="container">
      <!-- 面包屑 -->
      <XtxBread>
        <XtxBreadItem to="/">首页</XtxBreadItem>
        <XtxBreadItem v-if="goods" :to="`/category/${goods.categories[1].id}`">
          {{ goods.categories[1].name }}
        </XtxBreadItem>
        <XtxBreadItem
          v-if="goods"
          :to="`/category/sub/${goods.categories[0].id}`"
        >
          {{ goods.categories[0].name }}
        </XtxBreadItem>
        <XtxBreadItem v-if="goods">{{ goods.name }}</XtxBreadItem>
      </XtxBread>
      <!-- 商品信息 -->
      <div class="goods-info">
        <div class="media">
          <GoodsImage v-if="goods" :images="goods.mainPictures" />
          <GoodsSales />
        </div>
        <div class="spec">
          <GoodsName v-if="goods" :goods="goods" />
          <!-- sku规格组件 -->
          <GoodsSku
            v-if="goods && goods.specs"
            :goods="goods"
            @change="changeSku"
          />
          <!-- 数量选择组件 -->
          <XtxNumbox
            label="数量"
            v-if="goods"
            v-model="num"
            :max="goods.inventory"
          />
          <!-- 按钮组件 -->
          <XtxButton
            @click="insertCart()"
            v-if="goods"
            type="primary"
            style="margin-top: 20px"
          >
            加入购物车
          </XtxButton>
        </div>
      </div>
      <!-- 商品推荐 -->
      <GoodsRelevant v-if="goods" :goodsId="goods.id" />
      <!-- 商品详情 -->
      <div class="goods-footer">
        <div class="goods-article">
          <!-- 商品+评价 -->
          <GoodsTabs />
          <!-- 注意事项 -->
          <GoodsWarn />
        </div>
        <!-- 24热榜+专题推荐 -->
        <!-- 24热榜+专题推荐 -->
        <div class="goods-aside">
          <GoodsHot />
          <GoodsHot :type="2" />
          <GoodsHot :type="3" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import GoodsTabs from './components/goods-tabs.vue'
import GoodsWarn from './components/goods-warn.vue'
import GoodsHot from './components/goods-hot.vue'
import GoodsSku from './components/goods-sku'
import GoodsRelevant from './components/goods-relevant'
import GoodsSales from './components/goods-sales'
import GoodsName from './components/goods-name'
import GoodsImage from './components/goods-image.vue'
import { nextTick, provide, ref, watch } from 'vue'
import { findGoods } from '@/api/product'
import { useRoute } from 'vue-router'
import { useStore } from 'vuex'
import Message from '@/components/library/Message'
export default {
  name: 'XtxGoodsPage',
  components: { GoodsRelevant, GoodsImage, GoodsSales, GoodsName, GoodsSku, GoodsTabs, GoodsHot, GoodsWarn },
  setup () {
    // 1.获取商品详情进行渲染
    const goods = useGoods()
    const changeSku = (sku) => {
      if (sku.skuId) {
        goods.value.price = sku.price
        goods.value.oldPrice = sku.oldPrice
        goods.value.inventory = sku.inventory
      }
      // 记录选择后的sku，可能有数据，可能空对象
      currSku.value = sku
    }
    // 提供goods数据给后台组件使用
    provide('goods', goods)

    // 加入购物车
    const store = useStore()
    const currSku = ref(null)
    const insertCart = () => {
      if (currSku.value && currSku.value.skuId) {
        const { skuId, specsText: attrsText, inventory: stock } = currSku.value
        const { name, price, mainPictures, id } = goods.value
        // 约定加入购物车字段必须和后端保持一致
        store.dispatch('cart/insertCart', {
          skuId,
          attrsText,
          stock,
          name,
          price,
          picture: mainPictures[0],
          nowPrice: price,
          id,
          count: num.value,
          selected: true,
          // 是否有效
          isEffective: true
        }).then(() => {
          Message({ type: 'success', text: '成功加入购物车' })
        })
      } else {
        Message({ text: '请选择完整的规格' })
      }
    }
    // 选择的数量
    const num = ref(1)
    return {
      num,
      goods,
      changeSku,
      insertCart
    }
  }
}
// 获取商品详情
const useGoods = () => {
  // 出现路由地址商品发生变化，但是不会重新初始化组件
  const goods = ref(null)
  const route = useRoute()
  // 出现路由地址商品发生变化，会重新发起请求获取不同id的商品详情数据
  watch(() => route.params.id, (newValue) => {
    if (newValue && `/product/${newValue}` === route.path) {
      findGoods(route.params.id).then(data => {
        console.log('商品详情的数据：', data)
        // 置空，绑定v-if的组件就会被销毁
        goods.value = null
        // 加上nextTick，想让上一行代码生效，让组件渲染完成之后，再执行下一行代码
        nextTick(() => {
          goods.value = data.result
        })
      })
    }
  }, { immediate: true })

  return goods
}
</script>

<style scoped lang='less'>
.goods-info {
  min-height: 600px;
  background: #fff;
  display: flex;
  .media {
    width: 580px;
    height: 600px;
    padding: 30px 50px;
  }
  .spec {
    flex: 1;
    padding: 30px 30px 30px 0;
  }
}
.goods-footer {
  display: flex;
  margin-top: 20px;
  .goods-article {
    width: 940px;
    margin-right: 20px;
  }
  .goods-aside {
    width: 280px;
    min-height: 1000px;
  }
}
.goods-tabs {
  min-height: 600px;
  background: #fff;
}
.goods-warn {
  min-height: 600px;
  background: #fff;
  margin-top: 20px;
}
</style>
