<template>
  <div class="goods-sku">
    <dl v-for="item in goods.specs" :key="item.id">
      <!-- 规格名称 -->
      <dt>{{ item.name }}</dt>
      <dd>
        <!-- 具体属性 -->
        <template v-for="val in item.values" :key="val.name">
          <!-- 如果有图片显示图片，否则显示span -->
          <img
            :class="{ active: val.selected, disabled: val.disabled }"
            @click="changeSku(item, val)"
            v-if="val.picture"
            class="selected"
            :src="val.picture"
            alt=""
          />
          <span
            :class="{ active: val.selected, disabled: val.disabled }"
            @click="changeSku(item, val)"
            v-else
            >{{ val.name }}</span
          >
        </template>
      </dd>
    </dl>
  </div>
</template>
<script>
import powerSet from '@/vender/power-set'
// 分隔符
const spliter = '★'
// 得到一个路径字典
const getPathMap = (skus) => {
  // 1.拿到所有sku集合 props.goods.skus
  // 2.从所有的sku中筛选中有效的sku
  // 3.根据有效的sku使用power-set算法得到子集
  // 4.根据子集往路径字典对象中存储key-value
  // 数据字典
  const pathMap = {}
  skus.forEach(sku => {
    // 有效sku（有库存）
    if (sku.inventory > 0) {
      // 使用power-set算法得到子集
      // 可选值数组
      const valueArr = sku.specs.map(val => val.valueName)
      // 可选值数组子集
      const valueArrPowerSet = powerSet(valueArr)
      // console.log('可选值子集：', valueArrPowerSet)
      // 遍历子集，往pathMap插入数据
      valueArrPowerSet.forEach(arr => {
        // 将数组转换为字符串['蓝色','黑色']===>'蓝色★美国'
        // 拼接数组成为字符串 '蓝色★美国'（对象属性）
        const key = arr.join(spliter)
        // console.log('可选值子集数组拼接的字符串：', key)
        // 字典设置值(对象设置值)
        if (pathMap[key]) { // 字典里已经有这个属性
          pathMap[key].push(sku.id)
        } else { // 字典里没有这个属性
          pathMap[key] = [sku.id]
        }
      })
    }
  })
  return pathMap
}
// 获取选中的按钮属性
const getSelectedValues = (specs) => {
  const arr = []
  // item是每一行的规格
  specs.forEach(item => {
    // 找到选中的按钮
    const selectedVal = item.values.find(val => val.selected)
    // 如果按钮没选中，就设置为undefined ['蓝色'，undefined,undefind]
    arr.push(selectedVal ? selectedVal.name : undefined)
  })
  return arr
}
// 更新按钮禁用状态
const updateDisabledStatus = (specs, pathMap) => {
  // 1.约定每一个按钮的状态由本身的disabled数据来控制    item是规格(颜色)  val是值（'蓝色'）
  specs.forEach((item, i) => {
    // ['蓝色'，undefined,undefind]
    const selectedValues = getSelectedValues(specs)
    item.values.forEach(val => {
      // 2.判断当前是否选中，是选中不用判断 ['黑色']
      if (val.selected) return
      // 3.如果不是选中，拿当前的值顺序套入选中的值数组 ['蓝色'] ['中国']
      selectedValues[i] = val.name
      // 4.剔除undefined数据，按照分隔符拼接key       '蓝色' '蓝色☆中国'
      const key = selectedValues.filter(value => value).join(spliter)
      // 5.拿着key去路径字典中查找是否有数据，有可以点击，没有就禁用（true）
      val.disabled = !pathMap[key]// 找到了就是false
    })
  })
}
// 设置页面默认选中
const initDefaultSelected = (goods, skuId) => {
  // 1.找出你默认选中的sku的信息
  const sku = goods.skus.find(sku => sku.id === skuId)
  // 2.遍历每个按钮，按钮的值和sku记录的值相同，就选中
  goods.specs.forEach((item, i) => {
    const val = item.values.find(val => val.name === sku.specs[i].valueName)
    val.selected = true
  })
}
export default {
  name: 'GoodsSku',
  props: {
    goods: {
      type: Object,
      default: () => ({})
    },
    skuId: {
      type: String,
      default: ''
    }
  },
  setup (props, { emit }) {
    // 路径字典
    const pathMap = getPathMap(props.goods.skus)
    // 根据skuId初始化默认选中
    if (props.skuId) {
      initDefaultSelected(props.goods, props.skuId)
    }

    // console.log('路径字典', pathMap)
    // ☆组件初始化：更新按钮禁用状态
    updateDisabledStatus(props.goods.specs, pathMap)
    // 1.选中与取消选中,约定每个按钮都拥有自己的选中状态数据：selected
    // 1.1.当你点击的已选中，只需要取消当前选中
    // 1.2.点击的是未选中，把同一个规格的按钮改成未选中，当前点击的改成选中
    const changeSku = (item, val) => {
      // 当按钮时禁用的，阻止程序运行
      if (val.disabled) return
      // 如果不禁用正常执行
      if (val.selected) {
        val.selected = false
      } else {
        // 同一行的其他按钮改成未选中
        item.values.forEach(valItem => {
          valItem.selected = false
        })
        // 未选中本身按钮改成选中
        val.selected = true
      }
      // ☆ 组件初始化：更新按钮禁用状态
      // console.log(getSelectedValues(props.goods.specs))
      updateDisabledStatus(props.goods.specs, pathMap)
      // 将你选好的sku信息通知给父组件（skuId，price,oldPrice,inventory,specsText）
      // 1.选择完整的sku组合按钮，才能拿到这些信息，提交给父组件
      // 2.不是完整的sku结合按钮，提交空对象给父组件
      // validselectedValues 有效的 选择的 值数组
      const validselectedValues = getSelectedValues(props.goods.specs).filter(v => v)
      if (validselectedValues.length === props.goods.specs.length) {
        // console.log('选择完整')
        const skuIds = pathMap[validselectedValues.join(spliter)]
        const sku = props.goods.skus.find(sku => sku.id === skuIds[0])
        emit('change', {
          skuId: sku.id,
          price: sku.price,
          oldPrice: sku.oldPrice,
          inventory: sku.inventory,
          // 属性名：属性值 属性名：属性值...这样的字符串
          // .trim()去除首尾的空字符串
          // reduce p上一个回调函数返回的值，c 当前遍历的值   p' 颜色：蓝色'
          specsText: sku.specs.reduce((p, c) => `${p} ${c.name}:${c.valueName}`, '').trim()
        })
        //  颜色:蓝色 产地:中国 尺寸:10cm
        // console.log(sku.specs.reduce((p, c) => `${p} ${c.name}:${c.valueName}`, '').trim())
      } else {
        // console.log('不完整')
        // 给父组件空对象，是因为加入购物车的时候能进行判断
        emit('change', {})
      }
    }
    return {
      changeSku
    }
  }
}
</script>
<style scoped lang="less">
.sku-state-mixin () {
  border: 1px solid #e4e4e4;
  margin-right: 10px;
  cursor: pointer;
  &.active {
    border-color: @xtxColor;
  }
  &.disabled {
    opacity: 0.6;
    border-style: dashed;
    cursor: not-allowed;
  }
}
.goods-sku {
  padding-left: 10px;
  padding-top: 20px;
  dl {
    display: flex;
    padding-bottom: 20px;
    align-items: center;
    dt {
      width: 50px;
      color: #999;
    }
    dd {
      flex: 1;
      color: #666;
      > img {
        width: 50px;
        height: 50px;
        .sku-state-mixin ();
      }
      > span {
        display: inline-block;
        height: 30px;
        line-height: 28px;
        padding: 0 20px;
        .sku-state-mixin ();
      }
    }
  }
}
</style>
